import React, { Component } from "react";
import Clock from "./reloj.component";

class Inicio extends Component {
  render() {
    return (
      <React.Fragment>
        <div className="clock-box">
          <Clock />
        </div>

        <div styles={{ position: "absolute" }} className="container">
          <br />
          Componente de Inicio
          <br />
        </div>
      </React.Fragment>
    );
  }
}

export default Inicio;
