import "./App.css";
import publicIp from "public-ip";
import React, { Component } from "react";
import NoticiasRoutes from "./components/noticias.component";
import SunnahDelDia from "./components/sunnah.component";
import Inicio from "./components/inicio.component";
import Navigation from "./components/navigation.component";
import ConvertidorFechas from "./components/convertidorfechas.component";
import HorarioSalat from "./components/horariosalat.component";
import PalabraDelDia from "./components/palabradeldia.component";
import { BrowserRouter as Router, Route } from "react-router-dom";

const current = new Date();

export default class App extends Component {
  constructor() {
    super();
    this.getClientIp = this.getClientIp.bind(this);

    this.state = {
      ip: "",
      date: `${current.getDate()}-${
        current.getMonth() + 1
      }-${current.getFullYear()}`,
    };
  }
  async componentDidMount() {
    this.setState({ ip: await this.getClientIp() });
    localStorage.setItem("user_ip", this.state.ip);
  }

  getClientIp = async () =>
    await publicIp.v4({
      fallbackUrls: ["https://ifconfig.co/ip"],
    });

  render() {
    // console.log("App rendered. IP: ", this.state.ip);
    return (
      <div className="App">
        <Router>
          <Navigation />
          <Route path="/noticias" component={NoticiasRoutes} />
          <Route path="/aleya-hadiz-del-dia" component={SunnahDelDia} />
          <Route path="/horarios-salat" component={HorarioSalat} />

          <Route
            path="/fecha-gregoriana-a-hijri"
            render={(props) => (
              <ConvertidorFechas {...props} date={this.state.date} />
            )}
          />
          <Route path="/palabra-arabe-del-dia" component={PalabraDelDia} />
          <Route exact path="/" component={Inicio} />
        </Router>
      </div>
    );
  }
}
