import React from "react";
import RadioButton from "./radiobutton.sfc";

const Opciones = (props) => {
  return (
    <React.Fragment>
      <select
        className="form-control"
        onChange={(e) => props.onChangeMethod(e)}
      >
        {props.methods.map(function (method) {
          return (
            <option key={method.name} value={method.id}>
              {method.name}
            </option>
          );
        })}
      </select>
      <br />
      <div className="mb-1 form-check">
        <RadioButton
          onRadioChange={props.onRadioChange}
          radio={props.juristic[0]}
        />
        <RadioButton
          onRadioChange={props.onRadioChange}
          radio={props.juristic[1]}
        />
      </div>
      <br />
    </React.Fragment>
  );
};

export default Opciones;
