import React, { Component } from "react";
import Oracion from "./oraciones/oracion.sfc";
import axios from "axios";
import publicIp from "public-ip";
import Opciones from "./oraciones/opciones.sfc";

class HorarioSalat extends Component {
  constructor() {
    super();

    this.requestPrayerTimes = this.requestPrayerTimes.bind(this);
    this.handleMethodChange = this.handleMethodChange.bind(this);
    this.handleRadioChange = this.handleRadioChange.bind(this);

    this.state = {
      ip: localStorage.getItem("user_ip"),
      prayers: {
        times: ["1", " 1", "1", "1", "1", "1"],
        names: ["1", "1", "1", "1", "1", "1"],
        ids: [1, 3, 4, 5, 6],
      },
      juristic: [
        { name: "Hanafi", value: 1, default: true },
        { name: "Shafi, Hanbali, Maliki", value: 0, default: false },
      ],
      methodChosen: 2,
      juristicChosen: 1,
      methods: [
        { name: "ISNA - Islamic Society of North America", id: 2 },
        { name: "Mecca - Umm al-Qura", id: 4 },
        { name: "Egyptian General Authority of Survey", id: 5 },
        { name: "Karachi - University of Islamic Sciences", id: 1 },
        { name: "University of Tehran - Institute of Geophysics", id: 7 },
        { name: "MWL - Muslim World League", id: 3 },
      ],
    };
  }

  getClientIp = async () =>
    await publicIp.v4({
      fallbackUrls: ["https://ifconfig.co/ip"],
    });

  async componentDidMount() {
    this.setState({ ip: await this.getClientIp() });
    //console.log("IP (state): ", this.state.ip);
    this.requestPrayerTimes();
  }

  requestPrayerTimes() {
    /* API URL GUIDE: http://islamicfinder.us/index.php/api/index */
    //console.log("State Method Before API: ", this.state.methodChosen);
    axios
      .get(
        "http://islamicfinder.us/index.php/api/prayer_times?user_ip=" +
          this.state.ip +
          "&method=" +
          this.state.methodChosen +
          "&juristic=" +
          this.state.juristicChosen
      )
      .then((res) => {
        // console.log("Salat API: ", res.data.success);
        // console.log("Prayer Method: ", res.data.settings.method);
        // console.log("State Method After API: ", this.state.methodChosen);
        this.setState(() => ({
          prayers: {
            ids: [1, 3, 4, 5, 6],
            times: Object.values(res.data.results),
            names: Object.keys(res.data.results),
          },
        }));
      })
      .catch((err) => console.log(err));
  }

  handleMethodChange(e) {
    this.setState({ methodChosen: e.target.value });
  }

  handleRadioChange = (e) => {
    //console.log("Juristic Method", e.target.value);
    let juristic = this.state.juristic;
    juristic.forEach((jurist) => (jurist.default = !jurist.default));
    this.setState({
      juristicChosen: e.target.value,
      juristic,
    });
  };

  componentDidUpdate(prevProps, prevState) {
    if (prevState.methodChosen !== this.state.methodChosen) {
      this.requestPrayerTimes();
    } else if (prevState.juristicChosen !== this.state.juristicChosen) {
      this.requestPrayerTimes();
    } else if (prevState.ip !== this.state.ip) {
      this.requestPrayerTimes();
    }
  }

  render() {
    return (
      <React.Fragment>
        <div className="container" style={{ marginTop: "3em" }}>
          <Opciones
            juristic={this.state.juristic}
            methods={this.state.methods}
            onRadioChange={this.handleRadioChange}
            onChangeMethod={this.handleMethodChange}
          />
          <div className="card-columns custom-columns">
            {this.state.prayers.ids.map((id) => {
              //console.log(this.state.prayers.times[id - 1]);
              return (
                <Oracion
                  key={id}
                  oracionID={id}
                  salidaDelSol={this.state.prayers.times[1]}
                  nombre={this.state.prayers.names[id - 1]}
                  horario={this.state.prayers.times[id - 1]}
                />
              );
            })}
          </div>
        </div>
        <br />
      </React.Fragment>
    );
  }
}

export default HorarioSalat;
