import React, { Component } from "react";
import axios from "axios";

export default class ConvertidorFechas extends Component {
  constructor() {
    super();
    this.requestHijriDate = this.requestHijriDate.bind(this);
    const current = new Date();
    this.state = {
      date: `${current.getDate()}-${
        current.getMonth() + 1
      }-${current.getFullYear()}`,
    };
  }

  componentDidMount() {
    this.requestHijriDate();
  }

  requestHijriDate() {
    /* API URL GUIDE: https://aladhan.com/islamic-calendar-api */
    axios
      .get(" http://api.aladhan.com/v1/gToH?date=" + this.state.date)
      .then((res) => {
        // if (res.data.status === "OK") {
        //   // Peticion exitosa
        //   console.log("Fecha AH: ", res.data.data.hijri.date);
        // } else {
        //   console.log("Status Code: ", res.data.code);
        // }
        this.setState({
          hijri: {
            day: res.data.data.hijri.day,
            month: res.data.data.hijri.month.en,
            year: res.data.data.hijri.year,
          },
        });
      })
      .catch((err) => console.log(err));
  }

  render() {
    return (
      <div className="container">
        <br />
        Componente de Convertidor
      </div>
    );
  }
}
