import React from "react";

class Clock extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      time: new Date().toLocaleString().substring(10, 18),
    };
  }
  componentDidMount() {
    this.intervalID = setInterval(() => this.tick(), 1000);
  }
  componentWillUnmount() {
    clearInterval(this.intervalID);
  }
  tick() {
    this.setState({
      time: new Date()
        .toLocaleString()
        .substring(10, 18)
        .split(":")
        .join(" : "),
    });
  }
  render() {
    return <p className="clock">Son las {this.state.time}</p>;
  }
}

export default Clock;
