import React from "react";

const RadioButton = (props) => (
  <React.Fragment>
    <input
      type="radio"
      className="form-radio-input mr-1"
      value={props.radio.value}
      id={props.radio.value}
      onChange={(e) => props.onRadioChange(e)}
      name="asr-juristic"
      checked={props.radio.default}
    />
    <label className="form-radio-input mr-3" htmlFor={props.radio.name}>
      {props.radio.name}
    </label>
  </React.Fragment>
);

export default RadioButton;
