import React from "react";
import { Articulo, Articulo2 } from "./noticias/articulo.sfc";
import MainNoticias from "./noticias/mainnoticias.sfc";
import { Route } from "react-router-dom";

const NoticiasRoutes = ({ match }) => {
  return (
    <div>
      <Route exact path={match.url} component={MainNoticias} />
      <Route path={match.url + "/articulo-1"} component={Articulo} />
      <Route path={match.url + "/articulo-2"} component={Articulo2} />
    </div>
  );
};

export default NoticiasRoutes;
