import React from "react";

const formato = (props) => {
  let fstr = "";
  try {
    if (props.oracionID == 1) {
      fstr = props.horario.split("am").join("");
      fstr += "- " + props.salidaDelSol;
      return fstr.split("%").join("");
    }
    fstr += props.horario.split("%").join("");
  } catch (e) {
    //console.log(e);
    fstr += "";
  }
  return fstr;
};

String.prototype.format = function (params) {
  return this.replace(/\{(\w+)\}/g, function (a, b) {
    return params[b];
  });
};

const sunnahprayers = (key) => {
  let fard = 0;
  let sunna = 0;
  let rakas = "{fard} rakas obligatorias. {sunna} rakas Sunna";
  switch (key) {
    case 1:
      fard = 2;
      sunna = 2;
      break;
    case 3:
      fard = 4;
      sunna = 4;
      rakas = "{fard} rakas. Sunna {sunna} antes, 2 despuÃ©s";
      break;
    case 5:
      fard = 3;
      sunna = 2;
      break;
    case 6:
      fard = 4;
      sunna = 2;
      break;

    default:
      fard = 4;
      break;
  }
  return rakas.format({ fard, sunna });
};

const Oracion = (props) => {
  //console.log(props.salidaDelSol);
  return (
    <div className="card">
      <img
        src={"/assets/imgs/" + props.nombre + ".jpeg"}
        className="card-img-top"
        alt="..."
      />
      <div className="card-body">
        <h5 className="card-title">{"Hora " + formato(props)}</h5>
      </div>
      <div className="card-footer">
        <small className="text-muted">{sunnahprayers(props.oracionID)}</small>
      </div>
    </div>
  );
};

export default Oracion;
